"use client";

import { useEffect, useState } from "react";
import { API_BASE_URL } from "@/config/api";

interface Opportunity {
  id: number;
  title: string;
  description: string;
  fieldOfStudy: string;
  type: string;
  duration: string;
  prerequisites: string;
  pdfUrl: string;
}

export default function ProfessorDashboard() {
  const [opportunities, setOpportunities] = useState<Opportunity[]>([]);
  const [loading, setLoading] = useState(true);

  const [editModalOpen, setEditModalOpen] = useState(false);
  const [selectedOppId, setSelectedOppId] = useState<number | null>(null);
  const [pdfFile, setPdfFile] = useState<File | null>(null);

  useEffect(() => {
    fetchOpportunities();
  }, []);

  // Load opportunities
  const fetchOpportunities = async () => {
    try {
      // const res = await fetch(`${API_BASE_URL}/api/professor/opportunities`);
      // const data = await res.json();
      // setOpportunities(data);
      setOpportunities([
        {
          id: 1,
          title: "AI Research Internship",
          description: "Assist in ML research",
          fieldOfStudy: "AI/ML",
          type: "Internship",
          duration: "3 months",
          prerequisites: "Python, TensorFlow",
          pdfUrl: "/uploads/4d351a2f-5b4d-4668-85eb-3571b0daaf0a_test2.pdf",
        },
        {
          id: 2,
          title: "Updated Research on AI",
          description: "Now includes dataset section",
          fieldOfStudy: "ML",
          type: "Internship",
          duration: "5 months",
          prerequisites: "Python, TensorFlow",
          pdfUrl: "/uploads/637f5586-fdf3-4e2b-8e2d-60c2d02565dc_Ticket.pdf",
        },
        {
          id: 3,
          title: "Updated 3.0 Research on AI",
          description: "Now includes dataset section",
          fieldOfStudy: "ML",
          type: "Internship",
          duration: "5 months",
          prerequisites: "Python, TensorFlow",
          pdfUrl: "/uploads/139ae105-1b62-4e60-ac45-1aff61e52495_Ticket.pdf",
        },
      ]);
    } catch (e) {
      console.log("Fetch error: ", e);
    }
    setLoading(false);
  };

  // Delete
  const deleteOpportunity = async (id: number) => {
    if (!confirm("Are you sure you want to delete?")) return;

    try {
      const res = await fetch(
        `${API_BASE_URL}/api/professors/${id}`,
        {
          method: "DELETE",
        }
      );

      if (res.ok) {
        alert("Deleted successfully");
        fetchOpportunities();
      }
    } catch (e) {
      console.log("Delete error: ", e);
    }
  };

  // Open PDF Edit modal
  const openEditModal = (id: number) => {
    setSelectedOppId(id);
    setPdfFile(null);
    setEditModalOpen(true);
  };

  // Upload new PDF
  const updatePdf = async () => {
    if (!selectedOppId || !pdfFile) {
      alert("Upload a PDF file");
      return;
    }

    const fd = new FormData();
    fd.append("pdf", pdfFile);

    try {
      const res = await fetch(
        `${API_BASE_URL}/api/professor/opportunities/${selectedOppId}/pdf`,
        {
          method: "PUT",
          body: fd,
        }
      );

      if (res.ok) {
        alert("PDF updated!");
        setEditModalOpen(false);
        fetchOpportunities();
      } else {
        alert("Failed to update PDF");
      }
    } catch (e) {
      console.log("PDF update error:", e);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-r from-blue-50 to-indigo-50">
      {/* Top Navigation Bar */}
      <nav className="bg-gradient-to-r from-blue-600 to-indigo-600 text-white p-4 shadow-md flex justify-between items-center">
        <h1 className="text-xl font-bold">ProfConnect</h1>

        <div className="flex gap-3">
          <button
            onClick={() => (window.location.href = "/")}
            className="bg-white text-indigo-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-100 transition"
          >
            Home
          </button>

          <button
            onClick={() => {
              sessionStorage.removeItem("token");
              window.location.href = "/";
            }}
            className="bg-white text-indigo-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-100 transition"
          >
            Logout
          </button>
        </div>
      </nav>

      {/* Header */}
      <header className="bg-gradient-to-r from-blue-600 to-indigo-600 text-white p-5 shadow-md flex justify-between items-center">
        <h1 className="text-2xl font-bold">
          ProfConnect - Professor Dashboard
        </h1>
      </header>

      {/* Add Opportunity Button */}
      <section className="p-6">
        <a
          href="/professor/post-opportunity"
          className="bg-green-600 hover:bg-green-700 text-white font-semibold px-6 py-3 rounded-2xl shadow-md transition inline-block"
        >
          Post New Opportunity
        </a>
      </section>

      {/* Filter */}
      <section className="bg-white shadow-md p-6 sticky top-0 z-10 flex items-center gap-4">
        <label className="font-medium text-gray-700">Filter:</label>
        <select className="border rounded-lg px-3 py-2 focus:ring focus:ring-blue-200">
          <option value="">Select</option>
          <option>Institute Name</option>
          <option>Recently Added</option>
        </select>
      </section>

      {/* Opportunities */}
      <main className="p-6 grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {loading && <p>Loading...</p>}

        {!loading &&
          opportunities.map((opp) => (
            <div
              key={opp.id}
              className="opportunity-card bg-white rounded-2xl shadow-md p-6 hover:shadow-lg transition flex flex-col"
            >
              <h2 className="text-xl font-bold text-indigo-700 mb-2">
                {opp.title}
              </h2>

              <p className="text-gray-600 mb-1">
                <strong>Field:</strong> {opp.fieldOfStudy}
              </p>
              <p className="text-gray-600 mb-1">
                <strong>Type:</strong> {opp.type}
              </p>
              <p className="text-gray-600 mb-1">
                <strong>Duration:</strong> {opp.duration}
              </p>
              <p className="text-gray-600 mb-3">
                <strong>Pre-requisites:</strong> {opp.prerequisites}
              </p>

              <a
                href={`${API_BASE_URL}${opp.pdfUrl}`}
                target="_blank"
                className="text-blue-600 hover:underline text-sm mb-3"
              >
                View PDF Details
              </a>

              <div className="flex gap-2 mt-auto">
                <button
                  className="edit-btn bg-yellow-500 hover:bg-yellow-600 text-white rounded-lg px-4 py-2 transition"
                  onClick={() => openEditModal(opp.id)}
                >
                  Edit PDF
                </button>

                <button
                  className="delete-btn bg-red-500 hover:bg-red-600 text-white rounded-lg px-4 py-2 transition"
                  onClick={() => deleteOpportunity(opp.id)}
                >
                  Delete
                </button>
              </div>
            </div>
          ))}
      </main>

      {/* Edit PDF Modal */}
      {editModalOpen && (
        <div className="fixed inset-0 bg-opacity-60 backdrop-blur-sm flex justify-center items-center z-50">
          <div className="bg-white rounded-2xl shadow-2xl p-8 w-full max-w-lg transition-all">
            {/* Title */}
            <h2 className="text-xl font-bold mb-4">
              Edit PDF for Opportunity #{selectedOppId}
            </h2>

            {/* File Upload */}
            <div className="mb-6">
              <label className="block font-medium mb-2 text-gray-700">
                Upload New PDF
              </label>

              <input
                type="file"
                accept="application/pdf"
                className="w-full border border-gray-300 rounded-lg px-3 py-2 bg-gray-50 focus:ring focus:ring-indigo-200"
                onChange={(e) => setPdfFile(e.target.files?.[0] || null)}
              />
            </div>

            {/* Buttons */}
            <div className="flex justify-end gap-3">
              <button
                onClick={() => setEditModalOpen(false)}
                className="px-5 py-2 rounded-lg bg-gray-200 hover:bg-gray-300 text-gray-800 font-medium transition"
              >
                Cancel
              </button>

              <button
                onClick={updatePdf}
                className="px-5 py-2 rounded-lg bg-indigo-600 hover:bg-indigo-700 text-white font-medium transition"
              >
                Save Changes
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
